@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@300;400;600&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Outfit' 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



p {
  font-size: 1.2rem;
}

h1 {
  font-size: 6rem;
}

.app {
  width: 100%;
  min-height: 100vh; /* set from height to min-height */
  position: relative;
  background-color: rgba(0,0,0,.4);
  color: #fff;
}

.app:before {
  content: '';
  background: url('./assets/farmland.jpg') no-repeat center center/cover; /* couldnt see image, added no-repeat centre */
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;;
}

.app .search {
  text-align: center;
  padding: 1rem;
}

.app input {
  padding: .7rem 1.5rem;
  font-size: 1.2rem;
  border-radius: 25px;
  border: 1px solid rgba(255,255,255, 0.8);
  background: rgba(255,255,255, 0.1);
  color: #f8f8f8;
}

::placeholder {
  color: #f8f8f8;
}

.container {
  max-width: 700px;
  height: 80vh; /* changed from 700px to 80vh */
  margin: auto;
  padding: 0 1rem;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}


.app .top {
  width: 100%;
  margin: 1rem auto;
}

.app .description {
  position: relative;
  right: -90%;
  transform-origin: 0 0;  /* to rotate the description */
  transform: rotate(270deg);
}

.app .bottom {
  display: flex; /* place items next to each other */
  justify-content: space-evenly;  /* spread them out a bit better */
  text-align: center;
  width: 100%;
  margin: 1rem auto;
  padding: 1rem auto;
  border-radius: 12px;
  background-color: rgba(255,255,255,0.2);
}

.bold {
  font-weight: 700;
}


@media only screen and (min-width: 768px) {

  p {
    font-size: 1.6rem;
  }

}


@media only screen and (min-width: 1200px) {

  p {
    font-size: 2rem;
  }

}